import * as  types from '../mutation-types.js'
import vueGetData from "../../Js/vueGetData.js"

 //
const state = {
    pagesConfig:{
        totalpages:null,
        index:null
    }
}

//
const actions = {
    getTotal:function({commit},paramsJson){
        commit(types.GETLINELIST,list); //commitmutation-types
    }
}
//
const mutations = {
    [types.GETLINELIST](state , list){ //GETLINELISTT []
        state.lineslist = list;
    }
}

//
const getters = {
    totalpages(state){
        return state.pagesConfig;
    }
}

export default{
    state,
    actions,
    mutations,
    getters
}


