import * as  types from '../mutation-types.js'
import vueGetData from "../../Js/vueGetData.js"

 //
const state = {
    lineslist:[],
    productId:null
}

//
const actions = {
    getLines:function({commit},paramsJson){
        vueGetData.getData("productline",paramsJson,function(jsondata){
            if(jsondata.body.error_code === 22000){
                let list = jsondata.body.data;
                commit(types.GETLINELIST,list); //commitmutation-types
            }   
        },function(err){
            console.log(err)
        })
    },
    pushProductId: function({commit},paramsJson) {
        let id = paramsJson.id;
        commit(types.PRODUCTID,id)
    }
}
//
const mutations = {
    [types.GETLINELIST](state , list){ //GETLINELISTT []
        state.lineslist = list;
    },
    [types.PRODUCTID](state , id){
        state.productId = id;
    }
}

//
const getters = {
    lineslist(state){
        return state.lineslist;
    },
    productId(state){
        return state.productId;
    }
}

export default{
    state,
    actions,
    mutations,
    getters
}


